.cart-icon-header-number {
    display: flex;
    gap: 10px;
    position: relative;
    z-index: 2;
    cursor: pointer;
}

.cart-icon-header-number .number-cart-header{
    background-color: white;
    font-size: 16px;
    color: rgb(35, 35, 35);
    width: 29px;
    height: 29px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    position: absolute;
    top: -14px; right: -25px;
}

.cart-icon-header-number .cart-icon-header {
    height: 40px; width: 40px;
    display: flex;
    align-items: center; justify-content: center;
}

.cart-icon-header-number > div {
    pointer-events: none;
}

.open{
    color: rgb(222, 152, 22);
}

@media (max-width: 600px) {
    .cart-icon-header-number .number-cart-header{
        width: 24px;
        height: 24px;
        top: -15px; right: -20px;
    }
    
    .cart-icon-header-number .cart-icon-header {
        height: 31px; width: 31px;
    }
}